import React from 'react';
import styled from 'styled-components';
import { Button, Title2 } from '../Home/homeStyledComponents';
import { colors, mobileThresholdPixels } from '../styledComponents';
import Block from '../Home/Block';
import heart from '../../assets/heart.svg';
import schedule from '../../assets/schedule.svg';
import euro from '../../assets/euro.svg';
import smile from '../../assets/smile.svg';
import group from '../../assets/group.svg';
import shape from '../../assets/shape.svg';

const Container = styled.div`
  position: relative;
  text-align: center;
`;

const InvertedBlock = styled.div`
  display: flex;
  flex-direction: row;
  justify-content: center;
  padding: 20px 0px;
  text-align: left;

  @media (max-width: ${mobileThresholdPixels}) {
    flex-direction: column;
    padding: 2px 0px;
  }
`;

const P = styled.p`
  color: ${colors.navy};
  margin: 0px;
  @media (max-width: ${mobileThresholdPixels}) {
    text-align: justify;
    font-size: 12px;
  }
`;

const TheWhy = () => (
  <Container id="TheWhy">
    <Title2> le pourquoi </Title2>
    <InvertedBlock>
      <Block title="Flexibilit" icon={schedule} framed>
        <P>En fonction de votre emploi du temps, tilli vous confie des missions prs de chez vous.</P>
      </Block>
      <Block title="Transparence" icon={euro} framed>
        <P>{"Vous n'avez  ngocier avec votre client, nos prix sont fixs  l'avance."}</P>
      </Block>
    </InvertedBlock>
    <InvertedBlock>
      <Block title="Simplicit" icon={smile} framed>
        <P>{`tilli se charge de vous trouver des clients, de la facturation et de vos paiements scuriss.
          Tout est accessible depuis l'application mobile.`}</P>
      </Block>
      <Block title="Communaut" icon={group} framed>
        <P>{"Devenez auto-entrepreneur. N'ayez plus peur de travailler chez vous et partagez vos expriences."}</P>
      </Block>
    </InvertedBlock>
    <InvertedBlock>
      <Block title="Love" icon={heart}>
        <P>Bnficiez de bons plans, formations exclusives et de petites attentions chaque mois.</P>
      </Block>
      <Block title="Zro risque" icon={shape} framed>
        <P>{`Nous mettons en place une assurance rsponsabilit civile professionnelle qui vous permettra de travailler
          en toute scurit`}</P>
      </Block>
    </InvertedBlock>
    <a
      target="_blank" rel="noreferrer noopener"
      href="https://docs.google.com/forms/d/e/1FAIpQLScvR2ul5h35xj4qXnM0dTLj3cEkaRrmY-7Y9-eZyismNICHog/viewform"
    >
      <Button>{'> je veux tre couturier <'}</Button>
    </a>
  </Container>
);

export default TheWhy;



