import React, { PropTypes } from 'react';
import styled from 'styled-components';
import { colors, mobileThresholdPixels } from '../styledComponents';
import { Icon } from './homeStyledComponents';

const Container = styled.div`
  margin: 10px auto;
  flex: 1;
  text-align: center;
  max-width: 600px;
  display: flex;
  flex-direction: row;

  @media (max-width: ${mobileThresholdPixels}) {
    margin: 30px 5px;
  }
`;

const Body = styled.div`
  margin-top: 32px;
  margin-left: 20px;

  @media (max-width: ${mobileThresholdPixels}) {
    margin-top: 0px;
    margin-left: 5px;
  }
`;

const Title3 = styled.h3`
  text-align: center;
  width: 100%;
  min-width: 200px;
  color: ${colors.navy};
  font-size: 24px;
  font-weight: 300;
  margin: 0px;

  @media (max-width: ${mobileThresholdPixels}) {
    font-size: 16px;
    min-width: unset;
  }
`;

const Column = styled.div`
  display: flex;
  flex-direction: column;
  align-items: center;
`;

const Frame = styled.div`
  border: 1px solid ${colors.navy};
  border-radius: 50%;
  margin: 20px;
  width: 80px;
  height: 80px;
`;

const Block = ({ children, icon, title, framed }) => (
  <Container>
    <Column>
      {title && <Title3>{title}</Title3>}
      {!framed && icon && <Icon src={icon} alt="" framed={framed} />}
      {framed && icon && <Frame><Icon src={icon} alt="" framed={framed} /></Frame>}
    </Column>
    <Column>
      <Body>{children}</Body>
    </Column>
  </Container>
);

Block.propTypes = {
  icon: PropTypes.string,
  children: PropTypes.node.isRequired,
  title: PropTypes.string,
  framed: PropTypes.bool,
};

Block.defaultProps = {
  icon: '',
  title: '',
  framed: false,
};

export default Block;



