import React from 'react';
import styled from 'styled-components';
import { Button, Title2 } from '../Home/homeStyledComponents';
import { colors, mobileThresholdPixels } from '../styledComponents';
import NumerotedBlock from '../Home/NumerotedBlock';
import icon1 from '../../assets/1.svg';
import icon2 from '../../assets/2.svg';
import icon3 from '../../assets/3.svg';

const Container = styled.div`
  margin: 0px 20px;
  padding: 40px 0px 60px;
  background-color: ${colors.lightGrey};
  text-align: center;
  @media (max-width: ${mobileThresholdPixels}) {
    margin: 0px;
  }
`;

const Row = styled.div`
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  margin: 10px 0px;
  text-align: left;

  @media (max-width: ${mobileThresholdPixels}) {
    flex-direction: column;
  }
`;

const P = styled.p`
  color: ${colors.navy};
`;

const Title = styled.h3`
  color: ${colors.navy};
`;

const TheHow = () => (
  <Container id="TheHow">
    <Title2> le comment </Title2>
    <Row>
      <NumerotedBlock icon={icon1}>
        <Title>missions</Title>
        <P>{"Acceptez ou refusez les missions que l'on vous confie via notre application mobile."}</P>
        <P>  </P>
      </NumerotedBlock>
      <NumerotedBlock icon={icon2}>
        <Title>clients</Title>
        <P>{'Rendez-vous chez votre client et mettez votre savoir-faire  disposition.'}</P>
        <P>  </P>
      </NumerotedBlock>
      <NumerotedBlock icon={icon3}>
        <Title>paiements</Title>
        <P>{"Suivez vos paiements scuriss directement sur l'application mobile tilli."}</P>
        <P>  </P>
      </NumerotedBlock>
    </Row>
    <a
      target="_blank" rel="noreferrer noopener"
      href="https://docs.google.com/forms/d/e/1FAIpQLScvR2ul5h35xj4qXnM0dTLj3cEkaRrmY-7Y9-eZyismNICHog/viewform"
    >
      <Button>{'> je veux tre couturier <'}</Button>
    </a>
  </Container>
);

export default TheHow;



