import React, { PropTypes } from 'react';
import styled from 'styled-components';
import { mobileThresholdPixels } from '../styledComponents';
import { Icon, Title3 } from './homeStyledComponents';

const Container = styled.div`
  margin: 10px auto;
  flex: 1;
  text-align: center;
  max-width: 300px;
`;

const Body = styled.div`
  margin-top: 20px;

  @media (max-width: ${mobileThresholdPixels}) {
    margin-top: 0px;
  }
`;

const NumerotedBlock = ({ children, icon, title }) => (
  <Container>
    {title && <Title3>{title}</Title3>}
    {icon && <Icon src={icon} alt="" />}
    <Body>{children}</Body>
  </Container>
);

NumerotedBlock.propTypes = {
  icon: PropTypes.string,
  children: PropTypes.node.isRequired,
  title: PropTypes.string,
};

NumerotedBlock.defaultProps = {
  icon: '',
  title: '',
};

export default NumerotedBlock;



