import React from 'react';
import { Link } from 'react-router';
import styled from 'styled-components';
import { colors, mobileThresholdPixels } from '../styledComponents';
import logo from '../../assets/logo1.png';

const TextLink = styled.div`
  color: ${colors.navy};
  text-decoration: none;
  padding: 5px 10px;

  &:hover {
    transform: scale(1.1);
  }

  @media (max-width: ${mobileThresholdPixels}) {
    font-size: 10px;
    text-align: center;
  }
`;

const Row = styled.div`
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-items: flex-start;
  margin: 10px 0px;
`;

const Frame = styled.div`
  border-width: 1.5px;
  border-color: ${colors.navy};
  border-style: solid;
  text-align: center;

  &:hover {
    transform: scale(1.1);
  }
`;

const Column = styled.div`
  display: flex;
  flex-direction: column;
  align-items: center;
`;

const Subtext = styled.div`
  padding-top: 5px;
  font-style: italic;
  color: ${colors.navy};
  font-size: 10px;

  @media (max-width: ${mobileThresholdPixels}) {
    font-size: 9px;
  }
`;

const MenuBar = () => (
  <Row>
    <Link to={'/'}><TextLink><img src={logo} alt="logo" width="80" height="80" /></TextLink></Link>
    <Column>
      <Frame>
        <Link to={'/devenir-couturier'} style={{ textDecoration: 'none' }}>
          <TextLink href="">
            devenez couturier
          </TextLink>
        </Link>
      </Frame>
      <Subtext>Ne perdez plus le fil </Subtext>
    </Column>
    <Link><TextLink href="#MobileApps">{'se connecter'}</TextLink></Link>
  </Row>
);

export default MenuBar;



