import React from 'react';
import styled from 'styled-components';
import { Title2 } from './homeStyledComponents';
import { mobileThresholdPixels, colors } from '../styledComponents';
import NumerotedBlock from './NumerotedBlock';
import icon1 from '../../assets/1.svg';
import icon2 from '../../assets/2.svg';
import icon3 from '../../assets/3.svg';

const Container = styled.div`
  margin: 0px 20px;
  padding: 0px 0px 60px;
  color: ${colors.navy};
`;

const Row = styled.div`
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  margin: 10px 0px;

  @media (max-width: ${mobileThresholdPixels}) {
    flex-direction: column;
  }
`;

const HowItWorks = () => (
  <Container id="HowItWorks">
    <Title2> comment a marche </Title2>
    <Row>
      <NumerotedBlock icon={icon1}>
        Vous commandez votre retouche sur tilli.fr ou sur notre application.
      </NumerotedBlock>
      <NumerotedBlock icon={icon2}>
        {"Votre couturier se rend chez vous  l'heure de votre choix."}
      </NumerotedBlock>
      <NumerotedBlock icon={icon3}>
        Une fois prte, votre retouche est livre chez vous pour essayage.
      </NumerotedBlock>
    </Row>
  </Container>
);

export default HowItWorks;



